@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


html, body {
  min-height: 100vh;
}

body {
  color: #000;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5;
  background: url(https://i.imgur.com/8iVByha.jpg);
  background-attachment: fixed;
  background-repeat: round;
  background-clip: border-box;
}

input.main-inp {
  padding: 0.7rem 1.4rem;
  width: 100%;
  border-bottom: 2px solid #735fb3 !important;
  border: 0;
  background: rgb(254 247 247 / 73%);
  /* border: 2px solid #735fb3; */
  color: #271372;
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
  box-shadow: inset 0px 5px 20px 2px rgb(0 0 0 / 59%);
  opacity: 0.4;
  transition: ease-in box-shadow 0.5s;
}

input.main-inp:focus-visible {
  
    outline: none;
    opacity: 1;
    box-shadow: none;
    transition: ease-out box-shadow 0.3s;

}


section.section.weather-widg {
  background: rgb(23 21 21 / 16%);
  box-shadow: 0 8px 7px 0 rgb(31 38 135 / 37%);
  backdrop-filter: blur( 20px );
  -webkit-backdrop-filter: blur( 20px );
  /* border-radius: 10px; */
}

.location,.temp,.description{
  color: #fff;
  font-family: Poppins;
  font-size: 1rem;
}

.weather-card {
  width: fit-content;
  margin: 0 auto;
  background: #2C3E50;
  background: -webkit-linear-gradient(to right, #FD746C, #2C3E50);
  background: linear-gradient(to bottom, rgb(253 116 108 / 65%), rgb(44 62 80 / 63%));
  color: #fff;

  text-align: center;
  background-repeat: round;
}

.weather-int {
  padding: 2rem 5rem;
  /* background: pink; */
}

p.is-pulled-left,span.is-pulled-right {
  padding: 2%;
}

p.weather-main {
  font-weight: 800;
  font-size: calc(3.3em + (1.5 - 0.3) * ((100vw - 26em)/(37 - 18)));
  margin: 5%;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  /* backdrop-filter: blur(88px); */
  text-shadow: 0px 2px 5px rgb(0 0 0 / 68%);
}

p.weather-desc {
  margin-top: -9%;
  text-transform: capitalize;
  font-weight: 200;
  font-size: 1fr;
}

.flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 3%;
}
